import React, { Component } from 'react'
import {Link} from 'react-router-dom'
import axios from 'axios'

export default class GuideDocList extends Component {
    constructor(props) {
        super(props);
        this.state = {contents:[]};
        this.tmpNum = 0;
        this.url = makeUrl(this.props.match.params.category);
    }
    render() {
        return (
            <div id="contents" className="container" role="main">
               <strong> {this.tmpNum++}</strong>
               {this.state.contents.map((d,i)=>{ return (<ContentListBox d={d} key={i} />); })}
            </div>
        );
    }
    componentDidUpdate(nextProps, nextState) {
      if(nextProps.match.params.category !== this.props.match.params.category) {
        this.url = makeUrl(this.props.match.params.category);
        clearInterval(this.getList);
        timer(this, this.url);
        this.getList = setInterval(()=>{timer(this, this.url)},30000);
      }
    }
    componentDidMount() {
      timer(this, this.url);
      this.getList = setInterval(()=>{timer(this, this.url)},30000);
    }
    componentWillUnmount() {
      clearInterval(this.getList);
    }
}

const makeUrl = (category) => {
  return "https://s3.ap-northeast-2.amazonaws.com/hitthebuk.github.io/guidedoc/"+category+".json";
}

const timer = (thisis, url) => {
  axios
  .get(url)
  .then((res) => {
    thisis.setState({
      contents : res.data.contents
    });
  });
}

const ContentListBox = ({d}) => {
  return (
    <div><Link to={"/doc/"+d.category+"/"+d.idx}>{d.title}, {d.category}, {d.idx}, {d.desc}</Link></div>
  );
}



